.heading {
  font-size: 5rem;
  text-align: center;
}

.container {
  position: relative;
  max-width: 1200px;
  margin: 0 auto;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  padding: 2rem 0;
  padding-top: 9rem;
}



.searchButton {
  position: absolute;
  top: 3rem;
  left: 10rem;
  background-color: transparent;
  border-radius: 50%;
  font-size: 5rem;
  cursor: pointer;
  padding: 0;
  border: none;
  display: -ms-flexbox;
  display: flex;
  color: #0984e3;
  -webkit-transition: all .2s;
  -o-transition: all .2s;
  transition: all .2s;
}

.searchButton:hover {
  color: #0768b2;
  -webkit-transform: scale(1.1);
      -ms-transform: scale(1.1);
          transform: scale(1.1);
}



