.modal {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 100;
  width: 100%;
  height: 100%;
  background-color: rgba(0,0,0,0.4);
  overflow: hidden;
  background: -webkit-gradient(linear,left top, left bottom,from(rgba(#fff,0.2)),to(rgba(#fff,0.2)));
  background: -webkit-linear-gradient(rgba(#fff,0.2),rgba(#fff,0.2));
  background: -o-linear-gradient(rgba(#fff,0.2),rgba(#fff,0.2));
  background: linear-gradient(rgba(#fff,0.2),rgba(#fff,0.2));
}

.title {
  text-align: left;
  color: #fff;
  font-size: 3rem;
  padding: 1rem 0;
  margin: 0;
}

.description {
  color: #fff;
  width: 50%;
  font-size: 2rem;
}

.modalBox {
  padding: 3rem;
  width: 70%;
  height: 70%;
  border: 1.5rem solid #74b9ff;
  background-color: #2d3436;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%,-50%);
      -ms-transform: translate(-50%,-50%);
          transform: translate(-50%,-50%);
  -webkit-transform-origin: top;
      -ms-transform-origin: top;
          transform-origin: top;
  -webkit-animation-name: appear;
          animation-name: appear;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
}

.modalFig::after {
  content: "";
  background-image: -webkit-gradient( linear, left top, left bottom, from(rgba(0,0,0, .5)), to(rgba(0,0,0, .5)));
  background-image: -webkit-linear-gradient( rgba(0,0,0, .5), rgba(0,0,0, .5));
  background-image: -o-linear-gradient( rgba(0,0,0, .5), rgba(0,0,0, .5));
  background-image: linear-gradient( rgba(0,0,0, .5), rgba(0,0,0, .5));
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
}

.image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
}

.closeButton {
  border: none;
  background: transparent;
  color:#74b9ff;
  font-size: 5rem;
  cursor: pointer;
  position: absolute;
  top: 10px;
  right: 10px;
  -webkit-transition: all 0.2s;
  -o-transition: all 0.2s;
  transition: all 0.2s;
  z-index: 10;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  -webkit-transform: scale(1);
      -ms-transform: scale(1);
          transform: scale(1);

  display: -ms-flexbox;

  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.closeButton:hover {
  color: #0768b2;
  -webkit-transform: scale(1.1);
      -ms-transform: scale(1.1);
          transform: scale(1.1);
}

.info {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 10rem;
  background-color: rgba(0,0,0,.6);

  display: -ms-flexbox;

  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-align: center;
      align-items: center;
}

.infoBox {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}

.infoName {
  color: #0768b2;
  font-size: 3rem;
  text-transform: uppercase;
}

.infoContent {
  color: #74b9ff;
  font-size: 3rem;
}

@-webkit-keyframes appear {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@keyframes appear {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}


