.pagination {
  position: absolute;
  top: 3rem;
  right: 10rem;
  width: 25%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
}

.paginationButton {
  background-color: transparent;
  border-radius: 50%;
  font-size: 5rem;
  cursor: pointer;
  padding: 0;
  border: none;
  display: -ms-flexbox;
  display: flex;
  -webkit-transition: all .2s;
  -o-transition: all .2s;
  transition: all .2s;
  color: #0984e3;
  -webkit-animation-name: appear;
          animation-name: appear;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
}

.pages {
  font-size: 2rem;
}

.paginationButton:hover {
  color: #0768b2;
  -webkit-transform: scale(1.1);
      -ms-transform: scale(1.1);
          transform: scale(1.1);
}


@-webkit-keyframes appear {
  0% {
    -webkit-transform: scale(0);
            transform: scale(0);
  }

  80% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
  }

  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}


@keyframes appear {
  0% {
    -webkit-transform: scale(0);
            transform: scale(0);
  }

  80% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
  }

  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}




