.searchBar {
  position: absolute;
  top: 4rem;
  width: 40%;
  left: 15%;
  -webkit-animation-name: appear;
          animation-name: appear;
  -webkit-animation-duration: 2s;
          animation-duration: 2s;
  overflow: hidden;
}

.searchForm {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}

.searchInput {
  height: 3.5rem;
  font-size: 2rem;
  margin-right: 2px;
}

.button,
.buttonTopMovies {
  font-size: 2rem;
  text-transform: uppercase;
  border: none;
  background-color: #0984e3;
  color: #fff;
  cursor: pointer;
  padding: 5px 10px;
  -webkit-transition: all .2s;
  -o-transition: all .2s;
  transition: all .2s;
}

.button:hover,
.buttonTopMovies:hover {
  background-color:#0768b2;
}

.buttonTopMovies {
  position: absolute;
  right: 0;
  top: 1px;
}

@-webkit-keyframes appear {
  0% {
    max-width: 0;
  }

  100% {
    max-width: 100%;
  }
}

@keyframes appear {
  0% {
    max-width: 0;
  }

  100% {
    max-width: 100%;
  }
}


